let endX = ev.changedTouches[0].clientX\n        this.disX = endX - this.startX\n        this.translateX = 'translateX(0px)'\n        // 只有滑动大于页面宽度的10分之一距离才触发\n        if (Math.abs(this.disX) > (slideWidth / 10)) {\n          if (this.disX < 0) {\n            this.$emit('change', 'left')\n          } else {\n            this.$emit('change', 'right')\n          }\n        }\n      }\n    }\n  }\n}\n\n</script>\n<style>\n  .slide {\n    width: 100%;\n    height: 100%;\n  }\n</style>\n"],"sourceRoot":""}]);

