 || (toIndex === '0' && fromIndex === '0')) {\n          this.setPageDirection({dir: 'slide-left', to, from})\n        } else {\n          this.setPageDirection({dir: 'slide-right', to, from})\n        }\n      } else {\n        if (to.path !== '/home') {\n          ++historyCount\n          history.setItem('count', historyCount)\n          history.setItem(to.path, historyCount)\n          this.setPageDirection({dir: 'slide-left', to, from})\n        } else {\n          this.setPageDirection({dir: 'slide-right', to, from})\n        }\n      }\n      if (/\\/http/.test(to.path)) {\n        let url = to.path.split('http')[1]\n        window.location.href = `http${url}`\n      } else {\n        next()\n      }\n    })\n  }\n}\n</script>\n\n<style>\n  html,body{width: 100%;height: 100%;}\n  .vue-better-router-transition{\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    backface-visibility: hidden;\n    perspective: 1000;\n  }\n  .fade-enter-active{\n    animation: pageFadeIn 400ms forwards;\n  }\n  @keyframes pageFadeIn {\n    from {\n      opacity: 0;\n    }\n    to {\n      opacity: 1;\n    }\n  }\n  /*路由前进，退出*/\n\n  .slide-left-leave-active {\n    animation: pageFromCenterToLeft 400ms forwards;\n    z-index: 1;\n  }\n\n  /*路由后退，进入*/\n\n  .slide-right-enter-active {\n    animation: pageFromLeftToCenter 400ms forwards;\n    z-index: 1;\n  }\n\n  /*路由后退，退出*/\n\n  .slide-right-leave-active {\n    animation: pageFromCenterToRight 400ms forwards;\n    z-index: 10;\n    box-shadow: -3px 0  5px rgba(0,0,0,.1);\n  }\n\n  /*路由前进，进入*/\n\n  .slide-left-enter-active {\n    animation: pageFromRightToCenter 400ms forwards;\n    z-index: 10;\n    box-shadow: -3px 0  5px rgba(0,0,0,.1);\n  }\n\n  /*-------------------------------*/\n\n  /*路由前进，进入*/\n\n  @keyframes pageFromRightToCenter {\n    from {\n      /* left: 100%; */\n      transform: translate3d(100%, 0, 0);\n      opacity: 1;\n    }\n  